
class MarvelService {

	_apiBase = 'https://gateway.marvel.com:443/v1/public/';
	_apiKey = 'apikey=f612ab707ccc1b96d00e4e7b5fbbb0a0';
	_baseOffset = 210;

	getRecource = async (url) => {
		let res = await fetch(url);

		if(!res.ok) {
			throw new Error (`Could not fetch ${url}, status: ${res.status}`);
		}

		return await res.json();
	}

	getAllCharacters = async (offset = this._baseOffset) => {
		const res = await this.getRecource(`${this._apiBase}characters?limit=9&offset=${offset}&${this._apiKey}`);
		return res.data.results.map(this._transforCharacter)
	}

	getCharacter = async (id) => {
		const res = await this.getRecource(`${this._apiBase}/characters/${id}?${this._apiKey}`);
		return this._transforCharacter(res.data.results[0])
	}

	_transforCharacter = (char) => {
		return {
			id: char.id,
			name: char.name,
			description: char.description,
			thumbnail: char.thumbnail.path + '.' + char.thumbnail.extension,
			homepage: char.urls[0].url,
			wiki: char.urls[1].url,
			comics: char.comics.items
		}
	}
}

export default MarvelService;